import React, { Component } from "react";
import "./Home.css";

class Home extends Component {
  state = {
    firstRun: true,
    placeholder: "enter city",
    newCity: "tehran",
    City: "",
    date: "",
    country: "",
    condition: "",
    temp: "",
    forecastDay: [],
    image: "",
    searchValu: [],
    text: "",
    isDay: 0,
    humidity: "",
    feelslike: "",
    wind: ""
  };

  componentDidMount = async e => {
    if (this.state.newCity !== "") {
      const api = await fetch(
        `https://api.apixu.com/v1/forecast.json?key=1652ea732ca848b7bd6100429192205&q=${
          this.props.name
        }&days=6`
      );
      const data = await api.json();
      this.setState({
        City: data.location.name,
        country: data.location.country,
        date: data.location.localtime,
        temp: data.current.temp_c,
        image: data.current.condition.icon,
        condition: data.current.condition.text,
        text: "",
        placeholder: this.state.text,
        isDay: data.current.isDay,
        humidity: data.current.humidity,
        feelslike: data.current.feelslike_c,
        forecastDay: data.forecast.forecastday,
        wind: data.current.wind_kph
      });
      //console.log(this.props.name);
    }
  };

  dateHandler(item) {
    var d = new Date(item.date);
    var n = d.getDay();
    var day;
    switch (n) {
      case 0:
        day = "Sunday";
        break;
      case 1:
        day = "Monday";
        break;
      case 2:
        day = "Tuesday";
        break;
      case 3:
        day = "Wednesday";
        break;
      case 4:
        day = "Thursday";
        break;
      case 5:
        day = "Friday";
        break;
      case 6:
        day = "Saturday";
        break;
      default:
    }
    return day;
  }
  render() {
    return (
      <div className="body">
        <div className="base">
          <div className="location">
            {this.state.City}

            <p style={{ margin: "0", fontSize: "0.8rem" }}>
              {this.state.country}
            </p>
          </div>
          <div className="temp">
            <p className="tempc">{this.state.temp}</p>
            <p className="cantigrad">c</p>
            <p className="discription">{this.state.condition}</p>
          </div>
        </div>
        <div className="forcast-detail">
          <div className="forcast">
            <h2>Forecast</h2>
            <ul className="ul">
              {this.state.forecastDay.slice(0, 5).map((item, index) => (
                <li key={index}>
                  <div className="forcast-list">
                    <p className="date">{this.dateHandler(item)}</p>
                    <img
                      className="humidity-icon"
                      src="https://cdn1.iconfinder.com/data/icons/weather-line-icon-set-3/100/humidity-512.png"
                      alt="humidity"
                    />
                    <p className="humidity-number">{item.day.avghumidity}%</p>
                    <p className="maxtemp">{item.day.maxtemp_c}.c </p>
                    <p className="mintemp">{item.day.mintemp_c}.c</p>
                    <img
                      className="icon"
                      src={item.day.condition.icon}
                      alt=""
                    />
                  </div>
                  <p className="list-gap" />
                </li>
              ))}
            </ul>
          </div>
          <div className="detail">
            <img className="currentIcon" src={this.state.image} alt="icon" />
            <p className="list-gap" />
            <p>Feels like : {this.state.feelslike} c</p>
            <p className="list-gap" />
            <p>humidity : {this.state.humidity} %</p>
            <p className="list-gap" />
            <p>Wind speed : {this.state.feelslike} kmh</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
