import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Navbar from "./component/Navbar/Navbar";
import ChangeLocation from "./component/ChangeLocation/ChangeLocation";
import GetAPI from "./component/Home/GetAPI";
import "./App.css";

class Main extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          <Navbar />
          <Switch>
            <Route path="/weather" exact component={GetAPI} />
            <Route path="/weather/:name" exact component={GetAPI} />
            <Route path="/weather/add/name" component={ChangeLocation} />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default Main;
