import React from "react";
import Home from "./Home";

function GetAPI({ match }) {
  //console.log(match);
  if (
    match.params.name === "weather" ||
    match.params.name === "" ||
    match.params.name === ":name"
  ) {
    return <Home name="tehran" />;
  } else if (match.path === "/") {
    return <Home name="/tehran" />;
  } else return <Home name={match.params.name} />;
}
export default GetAPI;
